.Gameboard {
  display: flex;
  flex-flow: row wrap;
  justify-content: center;
  margin: auto;
  width: 550px;
  height: 560px;
}
.Status {
  height: 120px;
  text-align: center;
  font-family: monospace;
  background-color: rgba(0,0,0,0.5);
  color: red;
  line-height: 1;
}
.App {
  width: 800px;
  background-image: url(/whack-a-mole/static/media/grass-texture.1b92b004.png);
  background-repeat: repeat;
}

* {
  cursor: url(/whack-a-mole/static/media/mallet.016a06f6.png) 64 32, auto;
}

body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: aliceblue;
  display: flex;
  align-items: center;
  justify-content: center;
}
